@mixin col($name) {
	&[data-column*="#{$name}"] {
		@content; } }

.table {
	position: relative;
	display: table;
	table-layout: fixed;
	border: 1px solid rgba($darkGray, 0.2);
	width: 100%;
	background: $white;
	@include sm {
		display: block;
		border: none;
		background: none; }
	&__thead {
		display: table-header-group;
		@include sm {
			display: none; }
		.table__td {
			position: relative;
			padding-top: 10px;
			padding-bottom: 10px;
			background: rgba($darkGray, 0.2);
			font-size: 16px;
			font-weight: 500;
			line-height: 1.2;
			&[data-sorting] {
				padding-right: 20px;
				cursor: pointer;
				&:before,
				&:after {
					content: '';
					position: absolute;
					right: 5px;
					top: 50%; }
				&:before {
					@include arr(10, 6, $darkGray, t);
					transform: translate3d(0,-100%,0); }
				&:after {
					@include arr(10, 6, $darkGray, b);
					margin-top: 1px; } }
			&[data-sorting*="up"] {
				&:before {
					border-bottom-color: $blue; } }
			&[data-sorting*="down"] {
				&:after {
					border-top-color: $blue; } } } }
	&__tbody {
		display: table-row-group;
		@include sm {
			display: block;
			padding-bottom: 70px; }
		.table__tr:not(:last-child) {
			.table__td {
				border-bottom: 1px solid rgba($darkGray, 0.2);
				@include sm {
					border: none; } } }
		.table__td {
			&:before {
				content: attr(data-title);
				display: none;
				padding-bottom: 3px;
				color: lighten($darkGray, 30%);
				font-size: 16px;
				@include sm {
					display: inline-block;
					vertical-align: middle;
					padding-right: 10px; } } } }
	&__tr {
		position: relative;
		display: table-row;
		@include sm {
			display: block;
			padding: 20px 50px 20px 0;
			border-radius: 3px;
			background: rgba($darkGray, 0.05); }
		&.is-active {
			@include sm {
				padding-right: 0; }
			.table__field,
			.table__btns {
				display: block; }
			.table__edit,
			.table__text {
				display: none;
				@include sm {
					display: none; } }
			.table__text_static {
				display: block;
				@include sm {
					display: inline-block; } } } }
	&__td {
		display: table-cell;
		vertical-align: middle;
		padding: 5px 10px;
		@include sm {
			display: block;
			padding: 5px 15px; }
		@include col('edit') {
			width: 120px;
			@include sm {
				padding: 0;
				width: 100%; }
			&:before {
				@include sm {
					display: none; } } } }
	&__btns {
		margin: 0 -5px;
		@include clr;
		@include sm {
			padding-top: 20px;
			margin: 0 auto;
			max-width: 200px; } }
	&__btns-col {
		float: left;
		padding: 0 5px;
		width: 50%; }
	&__btn {
		width: 100%;
		height: 50px;
		background: none;
		font-size: 0;
		transition: background 0.25s, box-shadow 0.25s;
		@include hover {
			.icon {
				fill: $blue; } }
		.icon {
			display: inline-block;
			vertical-align: middle;
			fill: $darkGray;
			font-size: 20px;
			transition: fill 0.25s; } }
	&__save,
	&__cancel {
		@include sm {
			border: 1px solid #ccc;
			height: 40px; }
		// &:active
		// 	+sm
		// 		box-shadow: inset 2px 2px 2px rgba($black, 0.2)
		// .icon
		// 	+sm
 }		// 		fill: $white !important
	&__save {
		// +sm
		// 	background: $blue
		// &:active
		// 	+sm
 }		// 		background: darken($blue, 5%)
	&__cancel {
		// +sm
		// 	background: $red
		// &:active
		// 	+sm
		// 		background: darken($red, 5%)
		@include hover {
			.icon {
				fill: $red; } } }
	&__edit {
		@include sm {
			position: absolute;
			top: 10px;
			right: 0;
			width: 50px; } }
	&__field,
	&__btns {
		display: none; }
	&__text {
		font-size: 16px;
		line-height: 1.25;
		@include sm {
			display: inline-block;
			vertical-align: middle;
			padding: 10px 0; } }
	&__input {
		@include sm {
			height: 40px; } }
	&__nav {
		position: absolute;
		left: 50%;
		bottom: 0;
		margin-left: -110px;
		width: 220px;
		@include clr; }
	&__prev,
	&__next {
		border-radius: 3px;
		width: 50px;
		height: 50px;
		background: $blue;
		svg {
			display: inline-block;
			vertical-align: middle;
			height: 50%;
			fill: $white; } }
	&__prev {
		float: left;
		padding-right: 3px; }
	&__next {
		float: right;
		padding-left: 3px; }
	&__caption {
		position: absolute;
		left: 50%;
		bottom: 0;
		display: none;
		padding: 15px 0;
		margin-left: -60px;
		width: 120px;
		font-size: 20px;
		line-height: 1;
		text-align: center;
		@include sm {
			display: block; } } }
