.header {
	position: relative;
	color: $white;
	background: $blue;
	text-align: center;
	&__btn-help {
		right: 0;
		@include vertical;
		~ .header__inner {
			padding-right: 60px; }
		.icon {
			display: inline-block;
			vertical-align: middle;
			fill: currentColor;
			font-size: 30px; } }
	&__btn-nav {
		left: 0;
		~ .header__inner {
			padding-left: 60px; }
		&:before {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			margin: -1px 0 0 -14px;
			border-top: 2px solid $white;
			width: 28px; }
		span {
			position: absolute;
			top: 50%;
			left: 50%;
			margin: -11px 0 0 -14px;
			width: 28px;
			height: 22px;
			&:before,
			&:after {
				content: '';
				position: absolute;
				left: 0;
				border-top: 2px solid $white;
				width: 100%; }
			&:before {
				top: 0; }
			&:after {
				bottom: 0; } } }
	&__btn-help,
	&__btn-nav {
		position: absolute;
		top: 0;
		bottom: 0;
		width: 60px;
		color: $white;
		background: none;
		cursor: pointer;
		transition: background 0.25s, box-shadow 0.25s;
		@include hover {
			background: darken($blue, 10%); }
		&:active {
			background: darken($blue, 10%);
			box-shadow: inset 2px 2px 6px rgba(0,0,0,.2); } }
	// &__back
	// 	left: 0
	// 	+vertical
	// 	~ .header__inner
	// 		padding-left: 60px
	// 	&:active
	// 		box-shadow: inset -2px 2px 6px rgba(0,0,0,.2)
	// 	.icon
	// 		display: inline-block
	// 		vertical-align: middle
	// 		fill: $white
	// 		font-size: 24px
	&__inner {
		height: 60px;
		@include vertical; }
	&__title {
		display: inline-block;
		vertical-align: middle;
		@include f(18px, 500, 1.2);
		text-transform: uppercase;
		@include xs {
			font-size: 16px; } }
	&__logo {
		display: inline-block;
		vertical-align: middle;
		margin-left: 5px;
		width: 40px;
		height: 40px;
		@include xs {
			display: none; }
		&_vendor {
			margin-left: 10px;
			width: auto;
			height: 30px;
			@include xs {
				display: none; } } }
	&__logout {
		position: absolute;
		z-index: 1;
		top: 0;
		right: 0;
		bottom: 0;
		width: 120px;
		color: $white;
		background: none;
		@include vertical;
		cursor: pointer;
		transition: background 0.25s, box-shadow 0.25s;
		@include md {
			width: 60px; }
		@include hover {
			background: darken($blue, 10%); }
		&:active {
			background: darken($blue, 10%);
			box-shadow: inset 2px 2px 6px rgba(0,0,0,.2); }
		~ .header__inner {
			padding-left: 120px;
			padding-right: 120px;
			@include md {
				padding-left: 60px;
				padding-right: 60px; } }
		.icon {
			fill: currentColor; }
		.icon,
		span {
			display: inline-block;
			vertical-align: middle;
			font-size: 18px; }
		span {
			padding-left: 10px;
			@include md {
				display: none; } } } }
