.settings {
	&__section {
		&:not(:last-child) {
			margin-bottom: 20px; } }
	&__title {
		display: block;
		padding-bottom: 20px;
		color: inherit;
		font-size: 24px;
		font-weight: 700;
		transition: color 0.25s;
		@include hover {
			color: $blue; }
		&.is-active {
			.settings__arrow {
				transform: rotate(180deg); } } }
	&__arrow {
		margin-left: 0.5%;
		fill: currentColor;
		font-size: 50%;
		transition: transform 0.25s; }
	&__collapse {
		display: none; }
	&__item {
		padding: 5px 0;
		margin: 0 -5px;
		font-size: 0; }
	&__label,
	&__value {
		display: inline-block;
		vertical-align: middle;
		padding: 5px; }
	&__label {
		width: 200px;
		font-size: 16px;
		@include sm {
			width: 140px;
			font-size: 14px; } }
	&__value {
		width: calc(100% - 200px);
		max-width: 300px;
		@include sm {
			width: calc(100% - 140px); } }
	&__main {
		padding-top: 20px;
		@include clr; }
	&__sidebar {
		float: left;
		width: 300px;
		@include md {
			display: none; }
		~ .settings__content {
			margin-left: 320px;
			@include md {
				margin-left: 0; } } }
	&__search {
		max-width: none; }
	&__footer {
		margin: 0 -10px;
		font-size: 0;
		text-align: center; }
	&__btn {
		display: inline-block;
		vertical-align: top;
		padding: 10px;
		width: 50%;
		max-width: 200px; } }
