.header {
  position: relative;
  color: #fff;
  background: #2e99db;
  text-align: center;
}

.header__btn-help {
  right: 0;
  text-align: center;
  font-size: 0;
}

.header__btn-help:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.header__btn-help ~ .header__inner {
  padding-right: 60px;
}

.header__btn-help .icon {
  display: inline-block;
  vertical-align: middle;
  fill: currentColor;
  font-size: 30px;
}

.header__btn-nav {
  left: 0;
}

.header__btn-nav ~ .header__inner {
  padding-left: 60px;
}

.header__btn-nav:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -1px 0 0 -14px;
  border-top: 2px solid #fff;
  width: 28px;
}

.header__btn-nav span {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -11px 0 0 -14px;
  width: 28px;
  height: 22px;
}

.header__btn-nav span:before, .header__btn-nav span:after {
  content: '';
  position: absolute;
  left: 0;
  border-top: 2px solid #fff;
  width: 100%;
}

.header__btn-nav span:before {
  top: 0;
}

.header__btn-nav span:after {
  bottom: 0;
}

.header__btn-help, .header__btn-nav {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 60px;
  color: #fff;
  background: none;
  cursor: pointer;
  transition: background 0.25s, box-shadow 0.25s;
}

.no-touch .header__btn-help:hover, .no-touch .header__btn-nav:hover {
  background: #1f7db7;
}

.header__btn-help:active, .header__btn-nav:active {
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.header__inner {
  height: 60px;
  text-align: center;
  font-size: 0;
}

.header__inner:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
