.nav {
  position: fixed;
  z-index: 200;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  background: rgba(67, 76, 85, 0.8);
  transform: translate3d(0, 0, 0);
  transition: opacity 0.25s, visibility 0.25s;
  transition-delay: 0.2s;
}

.nav.is-open {
  opacity: 1;
  visibility: visible;
  transition-delay: 0s;
}

.nav.is-open .nav__inner {
  transform: translate3d(0, 0, 0);
  transition-delay: 0.2s;
}

.nav.is-open .nav__header,
.nav.is-open .nav__overlay {
  pointer-events: auto;
}

.nav__overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
}

.nav__inner {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  bottom: 0;
  overflow: auto;
  width: 300px;
  color: #fff;
  background: #434c55;
  box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.2);
  transform: translate3d(-100%, 0, 0);
  transition: transform 0.25s ease 0.25s;
  transition-delay: 0s;
}

.nav__header {
  position: relative;
  display: block;
  padding: 0 20px 0 50px;
  width: 100%;
  height: 60px;
  color: #fff;
  background: #2e99db;
  text-align: center;
  font-size: 0;
  text-align: left;
  cursor: pointer;
  pointer-events: none;
  transition: background 0.25s;
}

.nav__header:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.no-touch .nav__header:hover {
  background: #1f7db7;
}

.nav__header:active {
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.nav__header strong,
.nav__header .icon {
  display: inline-block;
  vertical-align: middle;
}

.nav__header strong {
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-transform: uppercase;
}

.nav__header .icon {
  position: absolute;
  top: 50%;
  left: 20px;
  margin-top: -0.5em;
  fill: currentColor;
  font-size: 20px;
}

.nav__header span {
  position: absolute;
  left: 50px;
  right: 0;
  bottom: 5px;
  font-size: 10px;
  font-weight: 400;
  line-height: 1;
}

.nav__body {
  padding: 20px;
}

.nav__user-data {
  padding-bottom: 20px;
}

.nav__list {
  margin: 0 -20px;
  border-top: 1px solid #4C5560;
  color: #7d838b;
}

.nav__link {
  position: relative;
  display: block;
  padding: 5px 20px 5px 50px;
  border-bottom: 1px solid #4C5560;
  color: inherit;
  font-size: 16px;
  font-weight: 400;
  line-height: 40px;
  transition: background 0.25s;
}

.no-touch .nav__link:hover {
  background: #4c5560;
}

.nav__link.is-active {
  color: #fff;
  background: #4c5560;
}

.nav__link .icon {
  display: block;
  margin: 0 auto;
  fill: currentColor;
}

.nav__icon {
  position: absolute;
  top: 50%;
  left: 5px;
  width: 40px;
  margin-top: -0.5em;
  font-size: 24px;
}

.switcher {
  display: inline-block;
  vertical-align: top;
  font-size: 0;
  user-select: none;
}

.switcher input {
  display: none;
}

.switcher input:checked ~ .switcher__control {
  background: #2e99db;
}

.switcher input:checked ~ .switcher__control:before {
  box-shadow: -1px 2px 2px rgba(0, 0, 0, 0.2);
  transform: translate3d(10px, 0, 0);
}

.switcher input:checked ~ .switcher__status [data-switcher="enabled"] {
  display: block;
}

.switcher input:checked ~ .switcher__status [data-switcher="disabled"] {
  display: none;
}

.switcher input:disabled ~ .switcher__label, .switcher input:disabled ~ .switcher__control, .switcher input:disabled ~ .switcher__status {
  cursor: not-allowed;
}

.switcher__label, .switcher__control, .switcher__status {
  display: inline-block;
  vertical-align: top;
  cursor: pointer;
}

.switcher__label, .switcher__status {
  padding: 5px 0;
  font-size: 16px;
  line-height: 1.3;
}

.switcher__label {
  margin-right: 10px;
  font-weight: 500;
}

.switcher__status {
  margin-left: 10px;
  font-weight: 400;
}

.switcher__status [data-switcher="enabled"] {
