.file-system {
  padding: 25px 10px 24px;
  border: 1px solid #ccc;
  background: #fff;
}

.file-system__control {
  position: relative;
  display: block;
  padding: 5px 10px;
  color: #333;
  font-size: 14px;
  cursor: pointer;
  transition: color 0.25s;
}

.no-touch .file-system__control:hover {
  color: #2e99db;
}

.no-touch .file-system__control:hover .file-system__folder {
  fill: #2e99db;
}

.no-touch .file-system__control:hover .file-system__arrow {
  border-left-color: #2e99db;
}

.no-touch .file-system__control:hover:before {
  background: #2e99db;
}

.file-system__control[data-system-control] {
  padding-left: 30px;
}

.file-system__control[data-system-control].is-hidden .file-system__folder_closed {
  display: block;
}

.file-system__control[data-system-control].is-hidden .file-system__folder_opened {
  display: none;
}

.file-system__control[data-system-control].is-hidden .file-system__arrow {
  transform: rotate(0deg);
}

.file-system__control_main {
  padding-left: 15px;
}

.file-system__control_main:before {
  content: '';
  position: absolute;
  top: 11px;
  left: 0;
  border-radius: 50%;
  width: 6px;
  height: 6px;
  background: #333;
  transition: background 0.25s;
}

.file-system__folder, .file-system__arrow {
  position: absolute;
  top: 50%;
  left: 10px;
}

.file-system__folder {
  margin-top: -0.6em;
  fill: #333;
  font-size: 13px;
  transition: fill 0.25s;
}

.file-system__folder_closed {
  display: none;
}

.file-system__arrow {
  margin-top: -5px;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 5px 0 5px 6px;
  border-color: transparent transparent transparent #333;
  transform: rotate(90deg);
  transition: border-color 0.25s;
}

.file-system__content[data-system-content] {
  display: block;
}

.file-system__content[data-system-content].is-hidden {
  display: none;
}

.file-system__content .file-system__content {
  padding-left: 20px;
}

.table {
  position: relative;
  display: table;
  table-layout: fixed;
  border: 1px solid rgba(51, 51, 51, 0.2);
  width: 100%;
  background: #fff;
}

@media only screen and (max-width: 767px) {
  .table {
    display: block;
    border: none;
    background: none;
  }
}

.table__thead {
  display: table-header-group;
}

@media only screen and (max-width: 767px) {
  .table__thead {
    display: none;
  }
}

.table__thead .table__td {
  position: relative;
  padding-top: 10px;
  padding-bottom: 10px;
  background: rgba(51, 51, 51, 0.2);
  font-size: 16px;
  font-weight: 500;
  line-height: 1.2;
}

.table__thead .table__td[data-sorting] {
  padding-right: 20px;
  cursor: pointer;
}

.table__thead .table__td[data-sorting]:before, .table__thead .table__td[data-sorting]:after {
  content: '';
  position: absolute;
  right: 5px;
  top: 50%;
}

.table__thead .table__td[data-sorting]:before {
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 0 5px 6px 5px;
  border-color: transparent transparent #333 transparent;
  transform: translate3d(0, -100%, 0);
}

.table__thead .table__td[data-sorting]:after {
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 6px 5px 0 5px;
  border-color: #333 transparent transparent transparent;
  margin-top: 1px;
}

.table__thead .table__td[data-sorting*="up"]:before {
  border-bottom-color: #2e99db;
}

.table__thead .table__td[data-sorting*="down"]:after {
  border-top-color: #2e99db;
}

.table__tbody {
  display: table-row-group;
}

@media only screen and (max-width: 767px) {
  .table__tbody {
    display: block;
    padding-bottom: 70px;
  }
}

.table__tbody .table__tr:not(:last-child) .table__td {
  border-bottom: 1px solid rgba(51, 51, 51, 0.2);
}

@media only screen and (max-width: 767px) {
  .table__tbody .table__tr:not(:last-child) .table__td {
    border: none;
  }
}

.table__tbody .table__td:before {
  content: attr(data-title);
  display: none;
  padding-bottom: 3px;
  color: gray;
  font-size: 16px;
}

@media only screen and (max-width: 767px) {
  .table__tbody .table__td:before {
    display: inline-block;
    vertical-align: middle;
    padding-right: 10px;
  }
}

.table__tr {
  position: relative;
  display: table-row;
}

@media only screen and (max-width: 767px) {
  .table__tr {
    display: block;
    padding: 20px 50px 20px 0;
    border-radius: 3px;
    background: rgba(51, 51, 51, 0.05);
  }
}

@media only screen and (max-width: 767px) {
  .table__tr.is-active {
    padding-right: 0;
  }
}

.table__tr.is-active .table__field,
.table__tr.is-active .table__btns {
  display: block;
}

.table__tr.is-active .table__edit,
.table__tr.is-active .table__text {
  display: none;
}

@media only screen and (max-width: 767px) {
  .table__tr.is-active .table__edit,
  .table__tr.is-active .table__text {
    display: none;
  }
}

.table__tr.is-active .table__text_static {
  display: block;
}

@media only screen and (max-width: 767px) {
  .table__tr.is-active .table__text_static {
    display: inline-block;
  }
}

.table__td {
  display: table-cell;
  vertical-align: middle;
  padding: 5px 10px;
}

@media only screen and (max-width: 767px) {
  .table__td {
    display: block;
    padding: 5px 15px;
  }
}

.table__td[data-column*="edit"] {
